<template>
  <section>
    <h1>{{ sportName }}</h1>
    <Card
      class="card"
      v-for="(item, index) in items"
      :key="index"
      :nr-of-runners="item"
      :section="sportName"
      :cardIndex="index"
      :buttons-selected="buttonsSelected"
      v-on:btn-click="onCardButtonClick">
    </Card>
  </section>
</template>

<script>
import Card from './Card';

export default {
  name: 'SportPage',
  props: {
    sportName: String,
    items: Array,
    buttonsSelected: Array
  },
  computed: {
    sectionName() {
      return `${this.sportName}-`;
    }
  },
  methods: {
    onCardButtonClick(btnId) {
      this.$emit('btn-click', btnId);
    }
  },
  components: {
    Card
  }
};
</script>

